





















































































































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class extends Vue {
   sortAscending = false;

   msg = '';
   created() {
      console.log(window.innerHeight);
   }
   breakpoint = this.$vuetify.breakpoint.name;

   // computed functions for responsive

   get chatBoxHeight() {
      switch (this.breakpoint) {
         case 'xs':
            return 100;
         case 'sm':
            return 46;
         case 'md':
            return 27;
         case 'lg':
            return 30;
         case 'xl':
            return 28;

         default:
            return 0;
      }
   }
   get listHeight() {
      switch (this.breakpoint) {
         case 'xs':
            return 100;
         case 'sm':
            return 46;
         case 'md':
            return 27;
         case 'lg':
            return 30;
         case 'xl':
            return 28;

         default:
            return 0;
      }
   }

   sendMsg(msg: string) {}
}
